import React, { Component } from 'react';
import '../../App.css';
import web3 from "../../ABI/web3";
import { Link } from 'react-router-dom';
import { Table, Card, Button, Modal, Progress } from 'antd';
import HangQinV3 from '../../ABI/hangqin';
import erc20abi from '../../ABI/ERC20.abi.json';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import nowTime from '../../ABI/nowTime';
const BigNumber = require('bignumber.js');

class Hangqin extends Component {
  state = {
    requests: [],
    HBid: '',
    HBpassword: '0',
    onoff: 'none',
    onoff2: 'none',
    lastHB: '',
    lastHBsymbol: '',
    message: "",
    visible: false,
    tokenadr: '',
    tokenName: '',
    MDEX: 'https://mdex.lg-qkl.xyz/',
    LGDEX: '',
    aa: 10,
    columns: [],
    columns2: [],
    percent: 0, //进度条
    percentDisplay: 'none',
    warnging: '',
    LPdisplay: 'none'
  };

  async componentDidMount() {
    const AllTokensName1 = await HangQinV3.methods.AllTokensName();
    console.log(AllTokensName1)

    const AllTokensName = await HangQinV3.methods.AllTokensName().call();
    const Allprice = await HangQinV3.methods.Allprice().call();
    const now = await nowTime.methods._now().call();
    const pushCook = await HangQinV3.methods.pushCook().call();
    var _LastNow = localStorage._now;
    var _LastAllprice = localStorage._Allprice
    var LastNow = Number(_LastNow)
    if (_LastNow === null || _LastAllprice === null || now >= LastNow + 86400 || pushCook !== localStorage.pushCook) {
      localStorage._now = now
      localStorage._Allprice = Allprice
      localStorage.pushCook = pushCook;
    }
    if (_LastAllprice !== undefined) {
      var LastAllprice = _LastAllprice.split(',')
    }
    var lastTime = Math.ceil((now - LastNow) / 3600)
    const AllTokens = await HangQinV3.methods.AllTokens().call();
    const dataSourceV3 = [];
    for (var i1 = 0; i1 < AllTokensName.length; i1++) {
      const price = [];
      price[0] = global.constants.tokenlistURI + AllTokensName[i1] + '.svg';
      price[1] = AllTokensName[i1];
      price[3] = new BigNumber((Allprice[i1] / LastAllprice[i1] - 1) * 100).toFixed(2) + '%';
      price[5] = AllTokens[i1];
      var p = 0;
      if (Allprice[i1].length < 16) {
        p = parseFloat(web3.utils.fromWei(Allprice[i1], "ether")).toExponential(2);
      } else {
        p = parseFloat(web3.utils.fromWei(Allprice[i1], "ether")).toFixed(3);
      }
      if (p > 1000) {
        p = parseFloat(Allprice[i1]).toFixed(0);
      }
      price[2] = p;
      dataSourceV3.push(price);
      this.setState({ message: "加载中……" + i1 + '=>' + AllTokensName.length });
    }
    this.setState({ message: "" });
    this.setState({ requests: dataSourceV3 });

    //设置MDEX链接
    const url = window.location.host;
    //设置LGDEX链接
    this.setState({ LGDEX: 'https://' + url + '/LGDEX' });

    const zhangdie = lastTime + 'H涨跌'

    const columns = [
      {
        title: '名',
        dataIndex: '0',
        width: 10,
        className: 'hangqing',
        align: 'right',
        render: (record) => <img src={record} alt='' width='28px' height='auto' />
      },
      {
        title: '称',
        dataIndex: '1',
        className: 'hangqing',
        align: 'left'
      },
      {
        title: '价格(￥)',
        dataIndex: '2',
        width: 80,
        align: 'right'
      },
      {
        title: zhangdie,
        dataIndex: '3',
        width: 70,
        align: 'center',
        render: (record) => {
          const _css = parseFloat(record) < 0 ? 'hangqingDie' : 'hangqingZhang';
          return (
            <div className={_css}>{record}</div>
          )
        }
      }
    ];
    const columns2 = [
      {
        title: '名',
        dataIndex: '0',
        width: 10,
        align: 'right',
        render: (record) => <img src={record} alt='' width='25px' height='auto' />
      },
      {
        title: '称',
        dataIndex: '1',
        align: 'left'
      },
      {
        title: '数量',
        dataIndex: '6',
        width: 100,
        align: 'left'
      },
      {
        title: '合￥',
        dataIndex: '4',
        align: 'left'
      }
    ];
    this.setState({ columns, columns2 })

  }

  ShowAssets = async () => {
    this.setState({ percent: 10, percentDisplay: 'block', columns: this.state.columns2 });
    const AllTokens = await HangQinV3.methods.AllTokens().call();
    this.setState({ percent: 20 });
    const data = this.state.requests;
    for (var i2 = 0; i2 < AllTokens.length; i2++) {
      const ERC20 = await new web3.eth.Contract(erc20abi.abi, AllTokens[i2]);
      const balance = await ERC20.methods.balanceOf(window.defaultAccount).call(); //原始余额用来计算单价 const balance =  await ERC20.methods.balanceOf(window.defaultAccount).call(7270799); 
      const decimals = await ERC20.methods.decimals().call();
      const __balance = parseFloat(balance * 10 ** (- decimals)) //减除了18位小数的余额
      //数量科学计数
      var balanceII = '';
      if (__balance > 100000) {
        balanceII = __balance.toExponential(1)
      } else {
        balanceII = __balance.toFixed(1)
      }

      //总价值科学计数
      var balanceRMB;
      balanceRMB = __balance * this.state.requests[i2][2]
      if (balanceRMB < 0.1) {
        balanceRMB = balanceRMB.toExponential(1)
      } else {
        balanceRMB = balanceRMB.toFixed(1)
      }
      if (__balance > 0) { data[i2][6] = balanceII }
      if (balanceRMB > 0) { data[i2][4] = balanceRMB }
      this.setState({ percent: this.state.percent + (80 / AllTokens.length).toFixed(0) });
    }
    this.setState({
      message: "",
      requests: data,
      percentDisplay: 'none'
    });
  }

  handleOk = () => {
    this.setState({ visible: false })
  };

  render() {
    const content = (
      <div>
        <b>通证名：{this.state.tokenName}&emsp;</b>
        <CopyToClipboard text={this.state.tokenadr}>
          <Button size='small' type='primary'>复制合约地址</Button>
        </CopyToClipboard>
      </div>
    );

    return (
      <div>
        <Card type="inner" size='small' style={{ borderRadius: '10px' }}>
          <div style={{ color: "green", fontSize: "13px", textAlign: 'center' }}>
            {this.state.message}
          </div>
          <div style={{ display: this.state.percentDisplay }}>
            <Progress percent={this.state.percent} status="active" />
          </div>
          <Table pagination={false} columns={this.state.columns} dataSource={this.state.requests}
            size='middle'
            onRow={(record) => {
              return {
                onClick: () => {
                  this.setState({
                    visible: true,
                    tokenadr: record[5],
                    tokenName: record[1],
                  })
                  if (record[1] === 'LG') {
                    this.setState({
                      warnging: 'LG为链改网的通证，我们不承诺该通证的价值，如果您购买请自担风险',
                      LPdisplay: 'none'
                    })
                  }
                  if (record[1] === 'LP') {
                    this.setState({
                      warnging: 'LP为链改网LG的映射通证（1LP=1000LG），我们不承诺该通证的价值，如果您购买请自担风险',
                      LPdisplay: 'block'
                    })
                  }
                  if (record[1] !== 'LP' && record[1] !== 'LG') {
                    this.setState({
                      warnging: '该通证为用户制作，请自行评估投资风险，如果您购买请自担风险',
                      LPdisplay: 'none'
                    })
                  }
                }
              }
            }}
          />
        </Card>
        <Card style={{ borderRadius: '10px' }}>
          <Button shape='round' type="primary" block color='green' size="middle"
            style={{ marginBottom: '10px' }}
            onClick={() => { this.ShowAssets() }}
          >显示我的资产</Button>
          <Button shape='round' block color='green' size="middle" ><Link to="/Hangqinadd">添加通证(全网可见)</Link></Button>
          <div >注：
            <div>2.8e-7是什么意思？</div>
            <div>科学计数法，2.8e-7=0.00000028</div>
          </div>

        </Card>
        <Modal visible={this.state.visible} onCancel={this.handleOk} footer={null}>
          <p>{content}</p>
          <p>
            风险提示：<br />{this.state.warnging}！
          </p>
          <div>
            <div style={{ display: this.state.LPdisplay, marginBottom: '5px' }}>
              <Button type='default' block style={{ marginTop: '5px' }}>
                <Link to="/LPToken">
                  与LG兑换
                </Link>
              </Button>
            </div>
            <div>
              {/* <Button type='default' style={{ float: 'left' }}>
                <a href={`${this.state.MDEX}/#/swap?outputCurrency=${this.state.tokenadr}`}>
                  MDEX交易
                </a>
              </Button> */}
              <Button type='primary' style={{ float: 'right' }}>
                <a href={`${this.state.LGDEX}/#/swap?inputCurrency=0x66a79D23E58475D2738179Ca52cd0b41d73f0BEa&outputCurrency=${this.state.tokenadr}`}>
                  LGDEX交易
                </a>
                {/* 如输入或输出为ETH，则inputCurrency=eth */}
              </Button>
              <div style={{ clear: 'both' }} />
            </div>
          </div>
        </Modal>
      </div>
    );
  }
}

export default Hangqin;
