import React, { Component } from 'react'
import { connect } from "react-redux"
import { getChainInfo } from '../../components/global/functions';
import HangQinHeco from './HangQinHeco';
import HangQinV2 from './HangQinV2';

export class HangQin extends Component {
  constructor(props) {
    super(props)
    this.state = {
        HangQinPage:<HangQinV2/>
    }
  }

  async componentWillReceiveProps(nextProps) {
    if ( nextProps.ChainID !== this.props.ChainID) {
      await this.fist()
    }
  }

  async componentDidMount() {
   await this.fist()
  }
  
  fist = async () => {
    await getChainInfo(window.ethereum.chainId).then((result) => {
      result.ChainName === 'HECO'? this.setState({HangQinPage: <HangQinHeco/>}) : this.setState({HangQinPage: <HangQinV2/>}) 
    })
  }

  render() {
    return (
      <div>
        {this.state.HangQinPage}
      </div>
    )
  }
}

const mapStateToProps = (state) => {
    return {
      ChainID: state.ChainID,
      UserAdr: state.UserAdr,
      LGbalance: state.LGbalance,
      ChainName: state.ChainName
    }
  }
  
  const mapDispatchToProps = (dispatch) => {
    return {
      sendStore(tp, val) {
        const action = { type: tp, value: val }
        dispatch(action)
      }
    }
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(HangQin)
  
